:root {
  --main-color: rgb(44, 117, 255);
  --secondary-color: rgb(255,60,25);
  --territiary-color: rgb(0,88,255);
}

html,body {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: var(--main-color);
}


.App {
  -ms-flex-direction: column;
      flex-direction: column;
  display: -ms-flexbox;
