  overflow: auto;
}

.wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: stretch;
      justify-content: stretch;
  -ms-flex-align: stretch;
      align-items: stretch;
  width: 100%;
